@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
body{margin:0;background-color:#FFD460}

body{margin:0;background-color:#FFD460}.card-root{width:80%;min-height:50px;display:grid;grid-template-columns:1fr 3fr;border-radius:4px;box-shadow:0px 4px 4px rgba(0,0,0,0.25);background-color:#fff;margin:auto;margin-bottom:10px}.card-root img{width:auto;-webkit-align-self:center;align-self:center;justify-self:center}.card-root .label{font-size:0.9rem}

body{margin:0;background-color:#FFD460}.home-root{max-height:100vh;overflow:hidden}.home-root .data-root{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:30px 10px}.home-root .data-root .profile-image{border-radius:50%;overflow:hidden}.home-root .data-root .profile-image img{width:auto;height:25vh;opacity:1}.home-root .data-root .text{margin-top:20px}.home-root .data-root .text p{margin:0;text-align:center;color:#2D4059;font-family:'Montserrat', sans-serif;text-shadow:0px 4px 4px rgba(0,0,0,0.25)}.home-root .data-root .text .title{font-size:2rem}.home-root .data-root .text .sub-title{margin:20px 0px 20px 0px}.home-root .data-root .links-root{padding:0px 70px;width:100%}.home-root .data-root .links-root .card-root:active{background-color:#9F8E60;-webkit-transform:translateY(4px);transform:translateY(4px)}.home-root .logo{opacity:0.4;z-index:-1}.home-root .mic{position:absolute;top:-5px;left:0px;-webkit-animation:mic-movement 1s ease-in-out alternate infinite;animation:mic-movement 1s ease-in-out alternate infinite}.home-root .mic img{height:auto;max-height:40vh;width:30vw}.home-root .music-symbol{position:absolute;bottom:50px;left:0px}.home-root .music-symbol img{width:100vw;height:auto}@-webkit-keyframes mic-movement{from{-webkit-transform:rotate(3deg);transform:rotate(3deg)}to{-webkit-transform:rotate(-3deg);transform:rotate(-3deg)}}@keyframes mic-movement{from{-webkit-transform:rotate(3deg);transform:rotate(3deg)}to{-webkit-transform:rotate(-3deg);transform:rotate(-3deg)}}


